<template>
    <div>
        <basic-header :items="['系统管理','密码修改']"></basic-header>
        <el-card style="margin-top: 20px">
            <el-col :span="8" :offset="7">
                <el-form  label-width="120px" ref="form" :rules="rules" :model="form">
                    <el-form-item label="旧密码" prop="password">
                        <el-input v-model="form.password"></el-input>
                    </el-form-item>
                    <el-form-item label="新密码" prop="password1">
                        <el-input v-model="form.password1"></el-input>
                    </el-form-item>
                    <el-form-item label="重复新密码" prop="password2">
                        <el-input v-model="form.password2"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submit">确定修改</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-card>
    </div>
</template>

<script>
    import MBXhead from "@/components/MBXhead";
    export default {
        data() {
            let v1=(rule,value,callback)=>{
                let reg=/^[0-9a-zA-Z]{6,}$/;
                if(reg.test(value)){
                    callback();
                }else{
                    callback(new Error("密码必须由6位以上的数字或字母组成"));
                }
            };
            let v2=(rule,value,callback)=>{
                if(value===this.form.password1){
                    callback()
                }else{
                    callback(new Error("两次密码必须一致"));
                }
            }

            return {
                form:{
                    username:sessionStorage.getItem('user'),
                    password:'',
                    password1:'',
                    password2:''
                },
                rules:{
                    password:{required:true,message:'请输入旧密码',trigger:'blur'},
                    password1:[{
                        validator:v1,
                        trigger:'blur',
                    },{
                        required:true,message:'请输入旧密码',trigger:'blur'
                    }],
                    password2:[{
                        validator:v2,
                        trigger:'blur',
                    },{
                        required:true,message:'请输入旧密码',trigger:'blur'
                    }],
                }
            }
        },
        components:{
            "basic-header":MBXhead
        },
        methods: {
            submit(){
                this.$refs.form.validate((val)=>{
                    if(val){
                        this.axios.put("api/Admin/changePassword",this.form).then(res=>{
                            if(res.data.code==200){
                                this.$message.success("修改成功");
                                this.$refs.from.resetFields()
                            }else{
                                this.$message.success(res.data.msg)
                            }
                        })

                    }else{
                        this.$message.error("修改失败")
                    }
                })
            }
        },
        created() {

        },
        mounted() {

        },
    }
</script>
<style scoped>

</style>